<template>
  <div class ="flex-div">
    <div class="login">
      <div v-if="error">
          <div class="alert alert-danger alert-dismissible fade show" role="alert">
            <strong>Authentification failed!</strong> {{error}}
            <button @click="noerr" type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
          </div>
        </div>

      <form @submit="sendForm">
        <label>
          <p class="label-txt">ENTER YOUR NAME</p>
          <input type="text" class="input"  v-model="username" required>
          <div class="line-box">
            <div class="line"></div>
          </div>
        </label>
        <label>
          <p class="label-txt">ENTER YOUR PASSWORD</p>
          <input  class="input"  v-model="password"  type="password" required>
          <div class="line-box">
            <div class="line"></div>
          </div>
        </label>
        <button type="submit" id="myBt">submit</button>
        <div id="some">don't have an account? <router-link to="/register">Register now</router-link></div>
      </form>
    </div>
  </div>
</template>
<script>

import {onLogin} from '../vue-apollo'
import { GET_LOGIN } from '../constants/graphql.js'

export default {
    name: 'SignIn',
    data(){
        return{
            login: null,
            user: null,
            error: null,
            username: null,
            password: null,
            er_mes:"",
            skipQuery:true
        };
    },
    methods:{
         async sendForm(e){
             e.preventDefault()
             let res=null
             try{
             res= await this.$apollo.query({
             query: GET_LOGIN,
                variables: { username: this.username,
                        password: this.password}
            })
            console.log(res.data.login)
            localStorage.setItem('username',this.username)
            await onLogin(this.$apollo.provider.defaultClient,res.data.login)
            this.$router.push('/chats')
            }
            catch({ graphQLErrors, networkError }){
                if (graphQLErrors)
                graphQLErrors.map(({ message}) =>
                this.error=message,)
                if (networkError) this.error=networkError
            }

        },
        noerr(){
            this.error=""
        }
    }
};
</script>
<style scoped>
body {
  background: #C5E1A5;
}
form {
  width: 55%;
  margin: 60px auto;
  background: #E0FFFF;
  padding: 60px 120px 80px 120px;
  text-align: center;
  font: small-caps bold 24px/1 sans-serif;
  -webkit-box-shadow: 2px 2px 3px rgba(0,0,0,0.1);
  box-shadow: 12px 10px 3px pink;
}
label {
  display: block;
  position: relative;
  margin: 40px 0px;
}
.label-txt {
  position: absolute;
  top: -1.6em;
  padding: 10px;
  text-align: center;
  font: small-caps bold 24px/1 sans-serif;
  font-size: .9em;
  letter-spacing: 1px;
  color: plum;
  transition: ease .3s;
}
.input {
  width: 100%;
  padding: 10px;
  background: transparent;
  border: none;
  outline: none;
}

.line-box {
  position: relative;
  width: 100%;
  height: 2px;
  background: blueviolet;
}

.line {
  position: absolute;
  width: 0%;
  height: 2px;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  background: aqua;
  transition: ease .6s;
}

.input:focus + .line-box .line {
  width: 100%;
}

.label-active {
  top: -3em;
}

#myBt {
  display: inline-block;
  padding: 12px 24px;
  background:  aqua;
  font-weight: bold;
  color: plum;
  border: none;
  outline: none;
  border-radius: 3px;
  cursor: pointer;
  transition: ease .3s;
}

#myBt:hover {
  background: blueviolet;
  color: aqua;
}
a:link{
   color: blueviolet;
}
#some{
   padding: 10px;
  font-family: sans-serif;
  font-size: .8em;
  letter-spacing: 1px;
  color: purple;
  transition: ease .3s;
}
</style>