  margin: 0 0 0 -27px;
}

.chat_ib h5{ 
  font-size:15px; 
  color: yellow; 
  margin:0 0 8px 0;
}

.chat_ib h5 span{ 
  font-size:13px; 
  float:right;
}

.chat_ib p{ 
  font-size:14px; 
  color:red; 
  margin:auto
}

.chat_img {
  float: left;
  width: 11%;
}
.chat_ib {
  float: left;
  text-align:left;
  font-size: 12pt;

}
#wer{
    text-align:right;
}

.chat_people{ 
  overflow:hidden; 
  clear:both;
}

.chat_list {
  border-bottom: 2px solid #6e8a60;
  margin: 0;
  padding: 18px 16px 10px;
}

.inbox_chat { 
  height: 550px; 
  overflow-y: scroll;
  }

.active_chat{ 
  background:#ccccff;
  }

.incoming_msg_img {
  display: inline-block;
  width: 6%;
}
.received_msg {
  display: inline-block;
  padding: 0 0 0 10px;
  vertical-align: top;
  width: 92%;
 }
 .received_withd_msg p {
  background: #6e8a60 none repeat scroll 0 0;
  border-radius: 3px;
  color: #ccccff;
  font: italic small-caps bold 16px/2 cursive;
  margin: 0;
  padding: 5px 10px 5px 12px;
  width: 100%;
}
.time_date {
  color: #bd959f;
  display: block;
  font-size: 12px;
  margin: 8px 0 0;
}
.received_withd_msg { 
  width: 57%;
}

.mesgs {
  float: left;
  padding: 30px 15px 0 25px;
  width: 100%;
}

 .sent_msg p {
  background: #ccccff none repeat scroll 0 0;
  border-radius: 3px;
  font: italic small-caps bold 16px/2 cursive;
  margin: 0; 
  color: #6e8a60;
  padding: 5px 10px 5px 12px;
  width:100%;
}
.outgoing_msg{ 
  overflow:hidden; 
  margin:26px 0 26px;
}
.sent_msg {
  float: right;
  width: 46%;
}
.input_msg_write input {
  background:  none repeat scroll 0 0;
  font-size: 20px;
  min-height: 60px;
  width: 100%;
}

.type_msg {
  border-top: 1px solid plum;
  position: relative;
  border: dotted plum;
}

.msg_send_btn {
  background: powderblue none repeat scroll 0 0;
  border-radius: 50%;
  border: thick double #32a1ce;
  cursor: progress;
  height: 40px;
  position: absolute;
  right: 0;
  top: 11px;
  width: 40px;
}
.messaging { 
  padding: 0 0 50px 0;
}

.msg_history {
  height: 516px;
  overflow-y: auto;
  background-color: #E0FFFF;
  border-right:3px solid plum;
}
#myBt {
  display: inline-block;
  padding: 12px 24px;
  background:aqua;
  font-weight: bold;
  color: purple;
  border: 4px dotted;
  outline: none;
  border-radius: 3px;
  cursor: pointer;
  transition: ease .3s;
}

#myBt:hover {
  background: purple;
  color: aqua;
}
.block-left{
    width:40%;
    height:800px;
    float:left;
}
#text{ 
    color:purple;
    font: 1.6em "Fira Sans", sans-serif;
    text-align: center;
    margin: 1pc;
}
.block-right{
    width:60%;
    height:800px;
    overflow:auto;
}
</style>